.header-wrapper {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
  width: 100%;
  height: 60px;
  line-height: 60px;
  padding: 0 30px;
  box-sizing: border-box;
  background: #fff;
  box-shadow: 0 0 8px rgba(0, 0, 0, .2);
}

.logo {
  float: left;
  font-size: 24px;
  letter-spacing: 2px;
}

.addition {
  float: right;
  font-size: 14px;
}

.addition span {
  margin-right: 30px;
}